.flex{
  display: flex;
}

.header-container{
  padding: 2rem 5rem;
  display: flex;
}

.logo{
  max-height: 6rem;
  position: fixed;
}

header{
/*  border-top: 5px solid #c79e5f;
  border-bottom:  5px solid #c79e5f;*/
  color: #c79e5f;
  position: fixed;
  z-index: 999;
  top: 0;
  width: 100%;
  margin-left: 50%;
  transform: translateX(-50%);
  backdrop-filter: blur(10px);
  user-select: none;
}

header .header-container{
  max-width: 130rem;
  margin: 0 auto;
}

header nav{
  list-style: none;
  display: flex;
  margin: 0 0 0 auto;
  min-height: 6rem;
}

header nav ul{
  list-style: none;
  display: flex;
  vertical-align: middle;
}

header nav ul li{
  font-size: 3rem;
  list-style: none;
  display: flex;
  padding-left: 5rem;
  align-items: center;
}

header nav ul li a{
  color: #111111;
  cursor: pointer;
  padding: 0 1rem;
  background: linear-gradient(rgba(246,246,246, 0) 50%, #ffcb74 50%);
  transition: all 1s ease;
  background-size: 100% 200%;
/*  -webkit-background-clip: text;*/
/*  -webkit-text-fill-color: transparent;*/
}

header nav ul li a:hover {
  background-position: bottom;
}

.landing{
  height: 100vh;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 1;
  cursor: default;
  background-image: url("./img/waves.svg");
  background-size: 100%;
  background-position: bottom;
  background-repeat: no-repeat;
}

.landing > div{
  margin: 0 auto;
/*  border: 5px solid #c79e5f;*/
}

.landing div > h1{
  font-size: 2.5rem;
  color: #2f2f2f;
  margin-left: 0.5rem;
  backdrop-filter: blur(10px);
  border-radius: 25px 25px 0 0;
  user-select: none;
  display: inline-block;
}

.landing div span{
  background-color: #ffcb74
}


.landing div > h2{
  font-size: 10rem;
  backdrop-filter: blur(10px);
  border-radius: 0 0 25px 25px;
  user-select: none;
}

.landing div .flex{
  margin-top: 4rem;
  justify-content: space-between;
  width: fit-content;
}

.landing nav{
  list-style: none;
  display: flex;
  margin: 0 auto;
  min-height: 6rem;
}

.landing nav ul{
  list-style: none;
  display: flex;
  vertical-align: middle;
}

.landing nav ul li{
  font-size: 2rem;
  list-style: none;
  display: flex;
  padding-left: 5rem;
  align-items: center;
}

.landing nav ul li a > *{
  display: inline-block;
  transform-origin: center center;
  color: #111111;
  margin-top: 5rem;
  font-size: 5rem;
  cursor: pointer;
  transition: all 1 ease;
  backface-visibility: hidden;
}

.landing nav ul li a > *:hover{
  -webkit-transform: scale(1.2);
  -moz-transform: scale(1.2);
  -o-transform: scale(1.2);
  -ms-transform: scale(1.2);
  transform: scale(1.2);
}

.landing div > div a{
  margin: 0 2rem;
}

.projects{
  position: relative;
  z-index: 1;
  cursor: default;
  width: 100%;
  margin-bottom: 100%;
}

.project-title{
  color: #c79e5f;
  font-size: 5rem;
}

.projects > div{
  width: 100%;
  text-align: center;
  border: 5px solid #c79e5f;
  background-color: #f6f6f6;
/*  backdrop-filter: blur(10px);
  background-image: url("./img/2.png");
  background-blend-mode: overlay;
  background-attachment: fixed;*/
}

.projects .project-list{
  margin: 0 auto 6rem auto;
  width: 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.project-face{
  border: 5px solid #c79e5f;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin: 6rem 1% 0 1%;
  width: 26%;
  min-width: 25rem;
  min-height: 15rem;
  text-align: center;
  border-radius: 10px;
  cursor: pointer;
  text-decoration: none;
}

.project-face > img{
  max-height: 100%;
  max-width: 100%;
  border-bottom: 5px solid #c79e5f;
}

.project-face > h3{
  width: 100%;
  padding: 2rem 0;
  font-size: 2.5rem;
  font-weight: normal;
  position: relative;
  vertical-align: middle;
  transition: .5s ease;
  color: #c79e5f;
}

.project > *{
  text-decoration: none;
  color: #c79e5f;
}

.project .voltar{
  position: absolute;
  padding: 0 2%;
  right: 0;
  top: 4%;
  z-index: 1;
  min-height: unset;
  border-left: 5px solid #c79e5f;
  border-right: 5px solid #c79e5f;
  border-top: 5px solid #c79e5f;
  border-radius: 0;
  font-size: 3rem;
  color: #9e7233;
  background: linear-gradient(#9e7233 50%, #c79e5f 50%);
  transition: all 1s ease;
  background-size: 100% 200%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.project .voltar:hover {
  background-position: bottom;
}

.project .voltar2{
  position: absolute;
  padding: 0 2%;
  right: 0;
  top: 99.5%;
  z-index: 1;
  min-height: unset;
  border-left: 5px solid #c79e5f;
  border-right: 5px solid #c79e5f;
  border-bottom: 5px solid #c79e5f;
  border-radius: 0;
  font-size: 3rem;
  background: linear-gradient(#9e7233 50%, #c79e5f 50%);
  transition: all 1s ease;
  background-size: 100% 200%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.project .voltar2:hover {
  background-position: bottom;
}

.project h1{
  font-size: 4rem;
  color: #c79e5f;
  margin-top: 2rem;
}

.project h2{
  min-width: 25rem;
  min-height: 15rem;
  text-align: center;
  border-radius: 10px;
  cursor: pointer;
  text-decoration: none;
}

.project p{
  font-size: 2rem;
  margin-bottom: 2rem;
}

.top-container{
  margin-top: 1rem;
}

.arrow-container{
   display: block;
   width: 100px;
   height: 100px;
   margin: 0 auto;
   background: linear-gradient(#9e7233 50%, #c79e5f 50%);
   transition: all 1s ease;
   background-size: 100% 200%;
   -webkit-background-clip: text;
   -webkit-text-fill-color: transparent;
}

.top-container .arrow-container{
  transform: scale(1.1)180deg);
}

.left-container .arrow-container{
  transform: scale(1.1)90deg);
}

.right-container .arrow-container{
  transform: scale(1.1)270deg);
}

.bottom-container .arrow-container{
  transform: scale(1.1)360deg);
  margin-top: -3rem;
  margin-bottom: 1rem;
}

.arrow-container:hover {
   cursor: pointer;
}
.arrow-container:hover .arrow {
   top: 50%;
}
.arrow {
   position: absolute;
   left: 50%;
   transition: all 0.4s ease;
}
.arrow:before, .arrow:after {
   transition: all 0.4s ease;
   content: '';
   display: block;
   position: absolute;
   transform-origin: bottom right;
   width: 4px;
   height: 25px;
   transform: scale(1.1)-45deg);
   background: #c79e5f;
}
.arrow:after {
   transform-origin: bottom left;
   transform: scale(1.1)45deg);
}
.arrow:nth-child(1) {
   opacity: 0.3;
   top: 35%;
}
.arrow:nth-child(2) {
   opacity: 0.6;
   top: 50%;
}
.arrow:nth-child(3) {
   opacity: 0.9;
   top: 65%;
}

.img-area{
  display: flex;
  margin: 0 auto;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.project .project-main{
  width: 70%;
  margin: 2rem 0;
  display: flex;
}

.project .img-container{
  max-width: 70%;
  margin: 0 auto;
}

.project img{
  width: 100%;
  max-height: 35vh;
  user-drag: none; 
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  border: 5px solid #c79e5f;
  position: relative;
  transition: all 0.5s linear;
}

.project .img-container::after{
  content: "Acessar Website";
  opacity: 0;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  min-width: 200px;
  border: 5px solid #c79e5f;
  border-radius: 10px;
  background-color: #f6f6f6;
  padding: 12px;
  color: #c79e5f;
  font-size: 1.4rem;
  z-index: 1;
  transition: all 0.5s linear;
}

.project .img-container:hover::after{
  opacity: 100;
}

.project img:hover,
.project .img-container:hover img{
  filter: brightness(0.5);
}

.project .text{
  margin-bottom: 1rem;
}

.project .text p{
  margin-bottom: 3rem;
}

.project .github{
  font-size: 2rem;
