}
.arrow-right {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
  width: 30px;
  height: 30px;
  border-right: 5px solid #fff;
  border-bottom: 5px solid #fff;
}

.bigarea{
  height: 100vh;
